$z-profile: 5;
$z-list: 4;
$z-monster: 3;

@keyframes flowerDance {
	0% {
		transform: rotate(0deg) skewY(0deg);
	}
	100% {
		transform: rotate(0deg) skewY(0deg);
	}

}
@keyframes profileBounce {
	0% {
		top: -122px;
	}
	60% {
		top: -114px;
	}
	100% {
		top: -122px;
	}
}

%flower-number{
	span{
		&:before{content:"";position: absolute;bottom: 48px;left: 50%;z-index: 1;}
	}
	&.num{
		@for $i from 1 through 30 {
			&#{$i}{span:before{margin-left: -(nth(nth($flower_number-sprites, $i), 5)/2);@include sprite(nth($flower_number-sprites, $i));}}
		}
	}
}
%flower-bud-number{
	span{text-align: center;
		&:before{content: "";margin-top:80px;display: inline-block;}
	}
	&.num{
		@for $i from 1 through 30 {
			&#{$i}{span:before{@include sprite(nth($flower_bud_number-sprites, $i));}}
		}
	}
}
%flower-bud2-number{
	span{text-align: center;
		&:before{content: "";margin-top:80px;display: inline-block;}
	}
	&.num{
		@for $i from 1 through 30 {
			&#{$i}{span:before{@include sprite(nth($flower_bud2_number-sprites, $i));}}
		}
	}
}
%flower-bud3-number{
	span{text-align: center;
		&:before{content: "";margin-top:80px;display: inline-block;}
	}
	&.num{
		@for $i from 1 through 30 {
			&#{$i}{span:before{@include sprite(nth($flower_bud3_number-sprites, $i));}}
		}
	}
}


